@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bonheur+Royale&display=swap');
//font-family: 'Bonheur Royale'

body{
  .menu{
    background-color: #111111;
    padding: 15px 0;
    position: sticky;
    top: 0;
    z-index: 1;
    .container{
      .top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .left{
          a{
            text-decoration: none;
            color: #111111;
            .brand{
              width: 70px;
              height: 70px;
              background-color: #fff;
              display: flex;
              align-items: center;
              justify-content: center;
              border-radius: 50%;
              cursor: pointer;
              &:hover{
                background-color: #818585;
                transition: all 0.3s linear;
              }
              h3{
                margin-bottom: 0;
                font-family:'Bonheur Royale', cursive;
                font-size: 60px;
                font-weight: 900;
              }
            }
          }
        }
        .hamburg{
          display: none;
          i {
            font-size: 30px;
            color: #ffffff;
            margin-right: 30px;
            transition: all 0.1s linear;
          }
        }
        .d-nonee{
          position: fixed;
          text-align: center;
          transition: all 0.5s linear;
          right: 0;
          top: 100px;
          width: 100%;
          background-color: #111111;
          left: 0;
          z-index: 999;
          .links{
            margin-bottom: 10px;
            a{
              //color: #ffffff;
              margin-top: 10px;
              background-color: #111111;
              display: block;
              align-items: center;
              font-family: "Promt";
              margin-right: 30px;
              font-size: 20px;
              font-weight: 400;
              text-decoration: none;
              transition: all 0.1s linear;
              &:hover{
                color:  #757575;
              }
            }
          }
          .social-links{
            margin-bottom: 10px;
            i{
              font-size: 30px;
              color: #ffffff;
              margin-right: 30px;
              transition: all 0.1s linear;
              &:hover{
                color:  #757575;
              }
              &:nth-child(3){
                margin-right: 0;
              }
            }
          }
        }
        .right{
          display: flex;
          justify-content: start;
          align-items: center;
          .menu-link{
            margin-right: 60px;
            a{
              //color: #ffffff;
              font-family: "Promt";
              margin-right: 30px;
              font-size: 20px;
              font-weight: 400;
              text-decoration: none;
              transition: all 0.1s linear;
              &:hover{
               color:  #757575;
              }
            }
          }

          .social-links{
            i{
              font-size: 30px;
              color: #ffffff;
              margin-right: 30px;
              transition: all 0.1s linear;
              &:hover{
                color:  #757575;
              }
              &:nth-child(3){
                margin-right: 0;
              }
            }
          }
        }
      }
    }
  }
  .main{
    padding-top: 100px;
    .container{
      .row{
        align-items: center;
        justify-content: space-between;
        .col-6{
          text-align: center;
          h2{
            font-weight: 900;
            font-size: 55px;
            font-family: "Promt";
            margin-bottom: 20px;
          }
          h3{
            font-family: "Promt";
            margin-left: 25%;
            margin-bottom: 20px;
            font-weight: 600;
            color: rgb(12, 83, 183);
            font-size: 2rem;
          }
          h5 {
            font-family: "Promt";
            font-size: 70px;
            font-weight: 900;
            margin-bottom: 20px;
            i {
              font-size: 30px;
            }
          }
          p{
              font-family: "Promt";
              font-weight: lighter;
              font-size: 20px;
            }
          figure{
              position: relative;
            background: #fff;
            overflow: hidden;
            height: 370px;
            padding-top: 10px;
              img{
                border-radius: 50%!important;
                width: 340px;
                height: 340px;
                cursor: pointer;
                transition: all 0.5s linear;
                box-shadow: 0 0 10px rgba(0,0,0,0.7);
              }
              &:before{
                position: absolute;
                top: 0;
                left: -75%;
                z-index: 2;
                display: block;
                content: '';
                width: 50%;
                height: 100%;
                background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
                background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
                -webkit-transform: skewX(-25deg);
                transform: skewX(-25deg);
              }
              &:hover:before{
                webkit-animation: shine 2s;
                animation: shine 2s;
              }
              @-webkit-keyframes shine {
                100% {
                  left: 125%;
                }
              }
              @keyframes shine {
                100% {
                  left: 125%;
                }
              }
            }
        }
        .col-8{
          margin-top: 50px;
          button{
            width: 80%;
            margin-left: 10%;
            background-color: #111111;
            color: #ffffff;
            font-family: "Promt";
            font-size: 22px;
            &:hover{
              color:  #757575;
            }
          }
        }
      }
    }
  }
  .about{
    .container{
      .row{
        padding-top: 50px;
        align-items: center;
        .col-6{
          text-align: center;
          h1{
            font-family: "Promt";
            font-size: 8.2rem;
            font-weight: 700;
            color: #333;
          }
          h6{
            font-size: 1.6rem;
            color: #757575;
            line-height: 1.3;
            font-family: "Promt";
            margin-bottom: 10px;
          }
          p{
            font-size: 1.1rem;
            font-family: "Promt";
          }
          figure{
            position: relative;
            background: #fff;
            overflow: hidden;
            height: 400px;
            padding-top: 10px;
            padding-bottom: 10px;
            img{
              border-radius: 50%!important;
              width: 380px;
              height: 380px;
              cursor: pointer;
              transition: all 0.5s linear;
              box-shadow: 0 0 10px rgba(0,0,0,0.7);
            }
            &:before{
              position: absolute;
              top: 0;
              left: -75%;
              z-index: 2;
              display: block;
              content: '';
              width: 50%;
              height: 100%;
              background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
              background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
              -webkit-transform: skewX(-25deg);
              transform: skewX(-25deg);
            }
            &:hover:before{
              webkit-animation: shine 2s;
              animation: shine 2s;
            }
            @-webkit-keyframes shine {
              100% {
                left: 125%;
              }
            }
            @keyframes shine {
              100% {
                left: 125%;
              }
            }
          }
        }
      }
      .skills{
        padding-top: 50px;
        .progress{
          margin-top: 10px;
          height: 2rem;
          h6{
            display: inline-block;
            margin: 0 10px;
          }
        }
      }
    }
  }
  .footer{
    margin-top: 50px;
    background-color: #111111;
    padding: 25px 0;
    .container{
      display: flex;
      justify-content: space-between;
      align-items: center;
      a{
        color: #ffffff;
        font-family: "Promt";
        margin-right: 30px;
        font-size: 20px;
        font-weight: 400;
        text-decoration: none;
        transition: all 0.1s linear;
        &:hover{
          color:  #757575;
        }
      }
    }
  }
  .portfolio{
    .row{
      &:nth-child(2){
       .col-6{
         margin-top: 25px;
         .card{
           box-shadow: 0 0 10px rgba(0,0,0,0.5);
         }
       }
      }
      padding-top: 50px;
      align-items: center;
      .col-6{
        text-align: center;
        h1{
          font-family: "Promt";
          font-size: 7.2rem;
          font-weight: 700;
          color: #333;
        }
        h6{
          font-size: 1.6rem;
          color: #757575;
          line-height: 1.3;
          font-family: "Promt";
          margin-bottom: 10px;
        }
        figure{
          position: relative;
          background: #fff;
          overflow: hidden;
          height: 420px;
          padding-top: 10px;
          padding-bottom: 10px;
          img{
            width: 400px;
            height: 400px;
            cursor: pointer;
            transition: all 0.5s linear;
            box-shadow: 0 0 10px rgba(0,0,0,0.7);
          }
          &:before{
            position: absolute;
            top: 0;
            left: -75%;
            z-index: 2;
            display: block;
            content: '';
            width: 50%;
            height: 100%;
            background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
            background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
            -webkit-transform: skewX(-25deg);
            transform: skewX(-25deg);
          }
          &:hover:before{
            webkit-animation: shine 2s;
            animation: shine 2s;
          }
          @-webkit-keyframes shine {
            100% {
              left: 125%;
            }
          }
          @keyframes shine {
            100% {
              left: 125%;
            }
          }
        }
        .card{
         .card-header{
           position: relative;
           &:hover .live{
             opacity: 1;
           }
           .live{
             position: absolute;
             background-color: rgba(0, 0, 0, 0.376);
             backdrop-filter: blur(3px);
             width: 100%;
             height: 100%;
             top: 0px;
             left: 0px;
             display: flex;
             flex-direction: column;
             -webkit-box-align: center;
             align-items: center;
             -webkit-box-pack: center;
             justify-content: center;
             gap: 2rem;
             opacity: 0;
             transition: all 0.3s linear 0s;
             button{
               padding: 10px 40px;
             }
           }
         }
          img{
            width: 100%;
            height: 300px;
          }
        }
      }
    }
  }
  .contact{
    .container{
      .first{
        padding-top: 50px;
        align-items: center;
        .col-6{
          h1{
            font-family: "Promt";
            font-size: 7.2rem;
            font-weight: 700;
            color: #333;
          }
          h6{
            font-size: 1.6rem;
            color: #757575;
            line-height: 1.3;
            font-family: "Promt";
            margin-bottom: 10px;
          }
          figure{
            position: relative;
            background: #fff;
            overflow: hidden;
           width: 100%;
            padding-top: 10px;
            padding-bottom: 10px;
            img{
             width: 100%;
              cursor: pointer;
              transition: all 0.5s linear;
            }
            &:before{
              position: absolute;
              top: 0;
              left: -75%;
              z-index: 2;
              display: block;
              content: '';
              width: 50%;
              height: 100%;
              background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
              background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
              -webkit-transform: skewX(-25deg);
              transform: skewX(-25deg);
            }
            &:hover:before{
              webkit-animation: shine 2s;
              animation: shine 2s;
            }
            @-webkit-keyframes shine {
              100% {
                left: 125%;
              }
            }
            @keyframes shine {
              100% {
                left: 125%;
              }
            }
          }
        }
      }
      .row{
        .form-group{
          textarea{
            height: 120px;
          }
        }
      }
    }
  }
  @media (max-width: 992px) {
    .main{
      .container{
        .row{
          .col-6{
            flex: 0 0 100%;
            max-width: 100%;

          }
        }
      }
    }
    .about{
      .container{
        .row{
          .col-6{
            flex: 0 0 100%;
            max-width: 100%;

          }
        }
      }
    }
    .portfolio{
      .container{
        .row{
          .col-6{
            flex: 0 0 100%;
            max-width: 100%;

          }
        }
      }
    }
    .contact{
      .container{
        .row{
          .col-6{
            flex: 0 0 100%;
            max-width: 100%;

          }
        }
      }
    }
  }
  @media (max-width: 768px) {
    .menu{
      .container{
        .top{
          .right{
            display: none;
          }
          .hamburg{
            display: block;
          }
        }
      }
    }
    .footer{
      .container{
        text-align: center;
        display: block!important;
        a{
          display: block;
          margin-top: 5px;
        }
      }
    }
  }
  @media (max-width: 500px){
    .main{
      .container{
        .row{
          .col-6{
            h2{
              font-size: 30px;
            }
            h3{
              margin-left: 11%;
            }
            h5{
              font-size: 45px;
            }

            figure{
              img{
                width: 290px;
                height: 290px;
              }
            }
          }
        }
      }
    }
    .about{
      .container{
        .row{
          .col-6{
            h1{
              font-size: 60px;
            }
            h3{
              margin-left: 11%;
            }
            h5{
              font-size: 45px;
            }

            figure{
              img{
                width: 290px;
