              }
            }
          }
        }
      }
    }
    .portfolio{
      .container{
        .row{
          .col-6{
            h1{
              font-size: 60px;
            }
            h6{
              font-size: 20px;
            }
            figure{
              height: 220px;
              img{
                width: 250px;
              }
            }
            img{
