          }
        }
      }
    }
  }
  @media (max-width: 300px){
    .main{
      .container{
        .row{
          .col-6{
            h2{
              font-size: 30px;
            }
            h3{
              margin-left: 5%;
            }
            h5{
              font-size: 45px;
            }
            p{
              font-size: 17px;
            }
            figure{
              height: 230px;
              img{
                width: 200px;
