              }
            }
          }
        }
      }
    }
    .about{
      .container{
        .row{
          .col-6{
            h3{
              margin-left: 5%;
            }
            h6{
              font-size: 20px;
            }
            p{
              font-size: 17px;
            }
            figure{
              height: 230px;
              img{
                width: 200px;
