              }
            }
          }
        }
      }
    }
    .portfolio{
      .container{
        .row{
          .col-6{
            h1{
              font-size: 45px;
            }
            h3{
              margin-left: 5%;
            }
            h6{
              font-size: 20px;
            }
            p{
              font-size: 17px;
            }
            figure{
              height: 190px;
             padding-left: 5px;
              padding-right: 5px;
              img{
